<template>
	<div>
		<div class="vx-row">
			<div v-for="item in estadisticas" :key="item.icono" class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/4 xl:w-1/4 mb-base">
				<statistics-card-line
					:icon="item.icono"
					:statistic="item.numero"
					:statisticTitle="item.titulo"
					:chartData="item.datos"
					:color="item.color"
					type='area' />
			</div>
		</div>
		<div class="vx-row">
			<div v-for="item in botones" :key="item.icono" class="vx-col w-full md:w-1/2 lg:w-1/2 xl:w-1/2">
               	<router-link :to="item.url">
					<statistics-card-line
						hideChart
						class="mb-base"
						:icon="item.icono"
						:statistic="item.titulo"
						:statisticTitle="item.subtitulo" />
               	</router-link>
            </div>
		</div>
	</div>
</template>

<script>
import StatisticsCardLine from '@/components/statistics-cards/StatisticsCardLine.vue';
import axios from 'axios';

export default {
  	components: {
		StatisticsCardLine
  	},
  	data() {
		return {
			botones:[
				{
					icono:"ListIcon",
					titulo:"LISTADO",
					color:"primary",
					subtitulo:"",
					url:"/listado",
				},
				{
					icono:"PlusIcon",
					titulo:"CREAR NUEVO",
					color:"primary",
					subtitulo:"",
					url:"/crear",
				},
			],
			estadisticas:[
				{
					icono: "SmileIcon",
					numero: 5,
					titulo: "Campa単as Activas",
					color: "primary",
					datos: [{
						name: 'Meses',
						data: [28, 40, 36, 52, 38, 60, 55]
					}]
				},
				{
					icono: "ClockIcon",
					numero: 7,
					titulo: "Campa単as Programadas",
					color: "success",
					datos: [{
						name: 'Meses',
						data: [28, 40, 36, 52, 38, 60, 55]
					}]
				},
				{
					icono: "CheckSquareIcon",
					numero: 45,
					titulo: "Campa単as Finalizadas",
					color: "danger",
					datos: [{
						name: 'Meses',
						data: [28, 40, 36, 52, 38, 60, 55]
					}]
				},
				{
					icono: "AwardIcon",
					numero: 4,
					titulo: "Campa単as Totales",
					color: "warning",
					datos: [{
						name: 'Meses',
						data: [28, 40, 36, 52, 38, 60, 55]
					}]
				},
			],
			subscribersGainedChartData: {
				chartOptions: {
					grid: {
						show: false,
						padding: {
							left: 0,
							right: 0
						}
					},
					chart: {
						toolbar: {
							show: false,
						},
						sparkline: {
							enabled: true
						}
					},
					dataLabels: {
						enabled: false
					},
					stroke: {
						curve: 'smooth',
						width: 2.5
					},
					fill: {
						type: 'gradient',
						gradient: {
							shadeIntensity: 0.9,
							opacityFrom: 0.7,
							opacityTo: 0.5,
							stops: [0, 80, 100]
						}
					},
					xaxis: {
						type: 'numeric',
						lines: {
							show: false,
						},
						axisBorder: {
							show: false,
						},
						labels: { show: false }
					},
					yaxis: [{
						y: 0,
						offsetX: 0,
						offsetY: 0,
						padding: { left: 0, right: 0 },
					}],
					tooltip: {
						x: { show: false }
					},
				},
			},
		}
	},
	methods: {
		cargarTotalesCampanias(){
			axios.get(this.url.totalesCampanias).then((res)=>{
				this.estadisticas[0].numero = res.data.activas;
				this.estadisticas[1].numero = res.data.programadas;
				this.estadisticas[2].numero = res.data.finalizadas;
				this.estadisticas[3].numero = res.data.total;
			}).catch((error) =>{
				this.$vs.notify({
					color:'danger',
					title:'Error al cargar Resumen',
					text: error,
					iconPack: 'feather', icon:'icon-alert-circle'
				});
			});
		}
	},
	computed:{
		url(){
			return this.$store.state.urlsAPI;
		},
		infoUser(){
			return this.$store.state.loginInfo;
		}
	},
	mounted() {
		this.cargarTotalesCampanias();
	},
}
</script>